    color: #243342;
    letter-spacing: 1.2px;
    font-size: 18px;
    border: 1px solid #243342;
    border-radius: 7px;
    margin-right: 20px;
    transition: 0.15s ease-in-out;
}
a.focus,
button{
    color: white;
    background: #254441;
