}

nav {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
    background-color: rgb(36, 51, 66);
}


nav .wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 15px 5%;
}
nav .hamburger {
    width: 30px;
    height: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    z-index: 3;
    opacity: 0;
    pointer-events: none;
}
nav .hamburger:hover{
    cursor: pointer;
}

nav .hamburger:hover span.slice {
    background-color: #b9bbbd;
}

nav .hamburger span.slice {
    display: block;
    width: 100%;
    height: 2px;
    background-color: white;
    transition: 0.2s ease-in-out;
}

nav.open .hamburger span.cheese.slice {
    opacity: 0;
}

nav.open .hamburger span.lettuce.slice {
    -webkit-transform-origin: center left;
            transform-origin: center left;
    -webkit-transform: rotate(43deg);
            transform: rotate(43deg);
}

nav.open .hamburger span.beef.slice {
    -webkit-transform-origin: center left;
            transform-origin: center left;
    -webkit-transform: rotate(-43deg);
            transform: rotate(-43deg);
}

nav.open .linkWrapper {
    opacity: 1;
    display: -webkit-flex;
    display: flex;
    pointer-events: initial;
}


nav h1 {
    color: white;
    font-weight: 300;
    font-size: 22px;
    letter-spacing: 1.2px;
}

nav a {
    color: white;
    text-decoration: none;
    display: inline-block;
    padding: 5px 15px;
    font-weight: 300;
    letter-spacing: 1.2;
    border: none;
    margin: 0;
}
a.active {
    color: #16f1dc;
    font-weight: 500;
}



